<template>
    <v-container class="py-10" fluid>
        <v-alert
            v-model="alert"
            :type="alertType"
            border="left"
            close-text="Close Alert"
            elevation="10"
            transition="scale-transition"
            style="
                position: fixed;
                top: 20%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 9999;
                max-width: 35rem;
            "
        >
            {{ alertMessage }}
        </v-alert>
        <v-row justify="center">
            <v-col cols="12" md="6">
                <v-card class="rounded-lg pb-2">
                    <v-toolbar color="green" dark flat>
                        <v-toolbar-title> Add Slideshow </v-toolbar-title>
                    </v-toolbar>
                    <v-card-text>
                        <v-row>
                            <v-col cols="12" md="6" class="pb-0">
                                <v-text-field
                                    v-model="name"
                                    label="Slideshow Name"
                                    hide-details="auto"
                                    background-color="white"
                                    solo
                                />
                            </v-col>
                            <v-col cols="12" md="6" class="pb-0">
                                <v-text-field
                                    v-model="delay"
                                    label="Slideshow Time"
                                    hide-details="auto"
                                    suffix="seconds"
                                    solo
                                />
                            </v-col>
                        </v-row>
                        <v-container class="my-4">
                            <draggable
                                v-model="slides"
                                group="imageGroup"
                                class="row"
                            >
                                <p v-if="slides.length == 0" class="mt-6">
                                    Drag images here, or upload an image
                                </p>
                                <v-col
                                    v-else
                                    cols="3"
                                    v-for="image in slides"
                                    :key="image.id"
                                >
                                    <v-card>
                                        <v-card-text>
                                            <v-img
                                                contain
                                                :src="image.thumbnailUrl"
                                            />
                                        </v-card-text>
                                        <v-card-actions>
                                            <v-btn
                                                color="warning"
                                                @click="removeSlide(image)"
                                            >
                                                Delete
                                            </v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-col>
                            </draggable>
                        </v-container>
                    </v-card-text>
                    <v-card-actions class="pt-3 pb-0">
                        <v-btn color="error" to="/terminals/slideshows">
                            Cancel
                        </v-btn>
                        <v-spacer />
                        <v-btn color="success" @click="addSlideshow()">
                            Add
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
            <v-col cols="12" md="6">
                <v-card class="rounded-lg pb-2">
                    <v-toolbar color="green" dark flat>
                        <v-toolbar-title> Image Library </v-toolbar-title>
                    </v-toolbar>
                    <v-card-title class="justify-center pt-2 pb-0 black--text">
                        Upload an Image
                    </v-card-title>
                    <v-file-input
                        class="px-5"
                        chips
                        counter
                        show-size
                        truncate-length="50"
                        accept="image/*"
                        label="Choose File"
                        v-model="originalFile"
                        hint="Recommended dimensions: 840 x 480"
                        persistent-hint
                    />
                    <v-card-subtitle class="pt-0 pb-1 text-center">
                        Upload png, jpg, gif
                    </v-card-subtitle>
                    <v-btn
                        right
                        @click="uploadMedia"
                        class="justify-center"
                        style="display: block; margin: 0 auto 1rem"
                    >
                        Upload
                    </v-btn>
                    <draggable
                        v-model="images"
                        :group="{
                            name: 'imageGroup',
                            pull: 'clone',
                            put: 'false',
                        }"
                        class="row"
                    >
                        <v-col cols="3" v-for="image in images" :key="image.id">
                            <v-card>
                                <v-img
                                    :src="image.thumbnailUrl"
                                    min-width="120"
                                />
                            </v-card>
                        </v-col>
                    </draggable>
                    <v-pagination
                        class="mt-6"
                        v-model="page"
                        circle
                        :length="pagesTotal"
                        :total-visible="7"
                        @input="getMediaLibrary"
                    ></v-pagination>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import draggable from 'vuedraggable';
import { mapGetters } from 'vuex';

export default {
    name: 'AddSlideshowScreen',
    components: {
        draggable,
    },
    data() {
        return {
            pagesTotal: 1,
            alert: false,
            alertMessage: '',
            alertType: 'error',
            images: [],
            page: 1,
            name: '',
            delay: '',
            slides: [],
            newImage: [],
            originalFile: null,
        };
    },
    mounted() {
        this.getMediaLibrary();
    },
    computed: {
        ...mapGetters(['getInternalCoreClearController']),
    },
    methods: {
        async getMediaLibrary(input) {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/get_media'}`,
                    {
                        params: { page: input },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    this.pagesTotal = response.data.pages;
                    console.log(response.data);
                    this.images = response.data.results;
                } else {
                    this.images = [];
                    console.log('Media library not found!');
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
            }
        },
        async addSlideshow() {
            // console.log(this.name);
            // console.log(this.delay);
            // console.log(this.slides);

            let formData = new FormData();
            formData.append('name', this.name);
            formData.append('delay', this.delay);

            this.slides.forEach((slide) => {
                formData.append('slides[]', JSON.stringify(slide));
            });

            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/update_slide_show'
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log('Post');
                    console.log(response);
                    this.alert = true;
                    this.alertType = 'success';
                    this.alertMessage = 'Slideshow added';
                    setTimeout(() => {
                        this.$router.push('/terminals/slideshows');
                    }, 1500);
                } else {
                    console.log('Media library not found!');
                }
            } catch (error) {
                console.error('Could not upload media');
                console.error(error);
            }
        },
        async uploadMedia() {
            console.log(this.originalFile);
            let formData = new FormData();
            formData.append(
                'upload_media',
                this.originalFile,
                this.originalFile.name,
            );

            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController + '/upload_media'}`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    },
                );

                if (response.data) {
                    this.getMediaAsset(response.data.id);
                } else {
                    console.log('Media library not found!');
                }
            } catch (error) {
                console.error('Could not upload media');
                console.error(error);
            }
            this.reset();
        },
        async getMediaAsset(id) {
            // console.log(id);
            try {
                let response = await axios.get(
                    `${
                        this.getInternalCoreClearController + '/get_media_asset'
                    }`,
                    {
                        params: { media_id: id },
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data) {
                    console.log(response.data);
                    this.newImage = {
                        description: response.data.description || '',
                        fileUrl: response.data.fileUrl,
                        id: response.data.id,
                        name: response.data.name || '',
                        originalFile: response.data.originalFile,
                        tags: response.data.tags || null,
                        thumbnailUrl: response.data.thumbnailUrl,
                        video: response.data.video || false,
                    };

                    this.slides.push(this.newImage);
                } else {
                    console.log('Media asset not found!');
                }
            } catch (error) {
                console.error('Could not get media library');
                console.error(error);
            }
        },
        removeSlide(slide) {
            const mediaIdToRemove = slide.id;

            const indexToRemove = this.slides.findIndex(
                (slide) => slide.id === mediaIdToRemove,
            );

            if (indexToRemove !== -1) {
                this.slides.splice(indexToRemove, 1);
            }
        },
    },
};
</script>
